.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 408px;
  padding: 0 16px;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 32px auto 0;
  display: block;
}

.product-card__title {
  color: #060b35;
  width: 166px;
  height: 36px;
  margin: 40px auto 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  margin: 4px 0 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__reviews {
  justify-content: space-between;
  align-items: flex-end;
  margin: 16px 0 0;
  display: flex;
}

.product-card__reviews-text {
  color: #060b35;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 0;
  display: flex;
}

.product-card__price-label {
  color: #616070;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-value {
  color: #060b35;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button-buy {
  color: #fff;
  text-transform: uppercase;
  box-sizing: border-box;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-card__button-buy:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
}

.star-rating {
  display: flex;
}

.star-rating__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.star-rating__star:last-child {
  margin-right: 0;
}

.star-rating--4 .star-rating__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Regular.ca197847.ttf");
  font-weight: 400;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.91322885.ttf");
  font-weight: 500;
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Bold.fdb9b54a.ttf");
  font-weight: 700;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
}
